/* ------navbar related styling -------*/
.nav-top{
  position:fixed;
  width:100%;
  z-index:1;
  top:0;
}

.navbar-item{
  margin-right: 30px
}

#navbar{
width:100%;
overflow:hidden;
display:block;
background-color: rgb(52, 47, 54);
color:#c0b5c0;
opacity:.9;
border-radius:0px;
padding: 2px;
font-size: 1.2em
}
#navbar:before,
#navbar:after{
content:"";
display:table;
clear:both;
}

#navLink{
float:right!important;
} 


#navLink:hover,
.navbar-item:hover{
  background-color: #e7d8d1; 
  color:rgb(52, 47, 54);
  text-decoration: none;
}


#home{
  position:relative;
}

#home:hover{
  display:block;
}

#navFixed{
  background-color: rgb(52, 47, 54)!important;
  color: #e7d8d1!important;
  display:block;
  width:100%;
  text-align:center;

}
/*for the script*/

.card{
  box-shadow:0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}
