.banner {
  position: absolute;
    background-color: #e7d8d1;
    color: black;
    bottom:0;
    height: 35%;
    top: 35%;
    left: 0%;
    right:0%;
    padding: 10px;
    opacity: .95;
    margin: 15px;
    font-size: .8em;
    border-top: 3px ridge transparent;
    border-bottom: 3px ridge transparent;
    border-image: linear-gradient(to right, #d18989, #7a7777);
    border-image-slice: 1;

}


.display-container{
  position: relative;
  height: 300px;
}

.display-topleft{
  position:absolute;
  left:0;
  top:0;
  background-color: #e7d8d1;
}


.banner:hover {
  opacity: 0;
}

#subtext{
  margin-top:10px
}

@media (max-width:700px){
  .banner{
      height: 55%;
      bottom: 0

  }
}

@media (max-width:700px) and (min-width:400px){
  .pfPic{
    height:250px
  }
  

}
